import * as d3 from "d3";
import { Data, Annotation } from "./types";
import { appendFile } from "fs";
var isArrayLike = require('validate.io-array-like');
var shortTimeFT = require("stft");

class LineChart {
    set data(newData: Data) {
        this._data = newData;
        if (this._data.ydatas.length > 10) {
            throw "Tried to render too many plots.";
        }
        this.render();
    };
    root: HTMLDivElement;
    patient: String;
    _data: Data;

    constructor(root: HTMLDivElement) {
        this.root = root;

        // Create default data
        let xdata = new Float32Array(d3.range(0, 60, 1 / 250));
        let ydatas = [];
        for (let i = 0; i < 4; i++) {
            let ydata = new Float32Array(15000);
            for (let j = 0; j < 15000; j++) {
                ydata[j] = Math.floor(Math.random() * 999999) + 1;
            }
            ydatas.push(ydata);
        }
        let annotations: Annotation[] = [];
        for (let i = 0; i < 5; i++) {
            annotations.push({
                name: "sz_on",
                onset: Math.random() * 15000 / 250,
            });
        }
        this._data = {
            xdata: xdata,
            ydatas: ydatas,
            annotations: annotations,
        }

        this.render();
        document.body.onresize = () => {
            this.render();
        }
    }

    static reduceAll(arr, fn) {
        if (isArrayLike(arr)) {
            return arr.reduce((a, b) => {
                return fn(this.reduceAll(a, fn), this.reduceAll(b, fn));
            });
        }
        return arr;
    }

    static minAll(arr): Number {
        return this.reduceAll(arr, Math.min);
    }

    static maxAll(arr): Number {
        return this.reduceAll(arr, Math.max);
    }


    render() {
        // TODO: do more layout in CSS, less in JS.
        const scrubberHeight = 100;
        const spectrogramHeight = 400;

        // Clear old chart elements
        while (this.root.firstChild) {
            this.root.removeChild(this.root.firstChild);
        }

        const style = document.createElement('style');
        style.textContent = `
        .scrubber-container {
            margin: auto;
        }

        .main-container {
            margin: auto;
        }
        
        .svg-plot, .canvas-plot {
            position: absolute;
        }

        .vline {
            pointer-events: all;
        }

        .vline:hover {
            stroke-width: 6px;
        }
      `;
        this.root.appendChild(style);

        let brush = d3.brushX();

        let wrapper = document.createElement('div');
        wrapper.setAttribute('class', 'scrubber-container');
        wrapper.style.setProperty('height', scrubberHeight + 'px')
        this.root.appendChild(wrapper);
        this.addChart(wrapper, true, brush, null);

        let gbrush = d3.select(wrapper).select(".brush");
        if (gbrush.empty()) {
            window.alert("empty!");
        }

        wrapper = document.createElement('div');
        wrapper.setAttribute('class', 'main-container');
        wrapper.style.setProperty('height',
            (this.root.getBoundingClientRect().height - scrubberHeight - spectrogramHeight) + 'px')
        this.root.appendChild(wrapper);
        this.addChart(wrapper, false, brush, gbrush);

        wrapper = document.createElement('div');
        wrapper.setAttribute('class', 'spectrogram-container');
        wrapper.style.setProperty('height', spectrogramHeight + 'px');
        this.root.appendChild(wrapper);
        this.addSpectrogram(wrapper, brush, gbrush);
    }

    addChart(
        wrapper: HTMLDivElement,
        scrubber: boolean,
        brush: d3.BrushBehavior<{}>,
        gbrush,
    ) {

        const xdata = this._data.xdata;
        const ydatas = this._data.ydatas;
        const annotations = this._data.annotations;
        const nys = ydatas.length;
        const container = d3.select(wrapper);
        let bbox = container.node().getBoundingClientRect();
        while (bbox.height === 0) {
            bbox = container.node().getBoundingClientRect();
        }
        const outerWidth = bbox.width;
        const outerHeight = bbox.height;
        const margin = {
            top: outerHeight / 20 / nys,
            right: 15,
            bottom: outerHeight / 10 / nys,
            left: 70
        };
        const width = outerWidth - margin.left - margin.right;
        const height = outerHeight - margin.top - margin.bottom;

        // Init Scales
        const xScale = d3.scaleLinear()
            .domain([LineChart.minAll(xdata), LineChart.maxAll(xdata)])
            .range([0, width]);

        brush.extent([[0, 0], [width, height]]);
        if (scrubber) {
            const brushChart = container.append('svg:svg')
                .attr('width', outerWidth)
                .attr('height', outerHeight)
                .attr('class', 'svg-plot')
                .style('z-index', 2)
                .append('g')
                .attr('transform', `translate(${margin.left}, ${margin.top})`);
            brushChart.append("g")
                .attr("class", "brush")
                .call(brush);
        }
        const annotationChart = container.append('svg:svg')
            .attr('width', outerWidth)
            .attr('height', height)
            .attr('class', 'svg-plot')
            .style('z-index', 1)
            .append('g')
            .attr('transform', `translate(${margin.left}, ${margin.top})`);
        if (!scrubber) {
            // Clip box to prevent lines from going off the chart.
            annotationChart
                .append('clipPath')
                .attr('id', 'clip-rectangle')
                .append('rect')
                .attr('x', 0)
                .attr('y', 0)
                .attr('width', width)
                .attr('height', height);
        }

        const extraPaddingForBottomAxis = 10;
        const subOuterHeight = (bbox.height - (scrubber ? 0 : extraPaddingForBottomAxis)) / nys;
        const subHeight = subOuterHeight - margin.top - margin.bottom;
        const yScale = d3.scaleLinear()
            .domain([LineChart.minAll(ydatas), LineChart.maxAll(ydatas)])
            .range([subHeight, 0])
            .nice();

        // Init Axes
        const xAxis = d3.axisBottom(xScale);
        const yAxis = d3.axisLeft(yScale);

        let contexts: Array<CanvasRenderingContext2D> = [];
        let gxAxis = null;
        for (let i = 0; i < nys; i++) {

            const div = container.append('div')
                .attr('width', outerWidth)
                .attr('height', subOuterHeight)
                .style('position', 'relative')
                .style('top', i * subOuterHeight + 'px');
            if (!scrubber && i === nys - 1) {
                div.attr('height', subOuterHeight + extraPaddingForBottomAxis);
            }

            // Init SVG
            let axisChart = div.append('svg:svg')
                .attr('width', outerWidth)
                .attr('height', div.attr('height'))
                .attr('class', 'svg-plot')
                .append('g')
                .attr('transform', `translate(${margin.left}, ${margin.top})`);

            // Init Canvas
            const canvasChart = div.append('canvas')
                .attr('width', width)
                .attr('height', subHeight)
                .style('margin-left', margin.left + 'px')
                .style('margin-top', margin.top + 'px')
                .attr('class', 'canvas-plot');

            const canvas = <HTMLCanvasElement>canvasChart.node()
            const context = canvas.getContext('2d');
            contexts.push(context);

            // Add Axis
            if (!scrubber && (i + 1 == nys)) {
                gxAxis = axisChart.append('g')
                    .attr('transform', `translate(0, ${subHeight})`)
                    .call(xAxis);
            }

            if (!scrubber) {
                const gyAxis = axisChart.append('g')
                    .call(yAxis);
            }
        }

        function draw(transform: d3.ZoomTransform) {
            const xScaleTranformed = transform.rescaleX(xScale);
            if (!!gxAxis) {
                gxAxis.call(xAxis.scale(xScaleTranformed));
            }

            const start = Math.floor(transform.x * -1 / transform.k / width * xdata.length);
            const end = start + Math.ceil(xdata.length / transform.k);

            window.requestAnimationFrame(function () {
                for (let y = 0; y < nys; y++) {
                    let context = contexts[y];
                    context.clearRect(0, 0, width, subHeight);
                    context.beginPath();
                    for (let i = start; i < end; i++) {
                        context.lineWidth = 1.0;
                        context.strokeStyle = "steelblue";

                        const px = xScaleTranformed(xdata[i]);
                        const py = yScale(ydatas[y][i]);

                        if (i === start) {
                            context.moveTo(px, py);
                        } else {
                            context.lineTo(px, py);
                        }
                    }
                    context.stroke();
                }
            });
        }
        draw(d3.zoomIdentity);

        function updateAnnotations(transform: d3.ZoomTransform) {
            const xScaleTranformed = transform.rescaleX(xScale);
            let vlines = annotationChart.selectAll("line")
                .data(annotations);

            vlines.enter().append("line")
                .attr("class", "vline")
                .merge(vlines)
                .attr("x1", a => xScaleTranformed(a.onset))
                .attr("x2", a => xScaleTranformed(a.onset))
                .attr("y1", 0)
                .attr("y2", height)
                .attr("stroke", "#ffc107")
                .attr("stroke-width", "3px")
                .attr('clip-path', 'url(#clip-rectangle)')
                .append("title").text(a => a.name);

            vlines.exit().remove();
        }
        updateAnnotations(d3.zoomIdentity);

        var zoom = d3.zoom()
            .scaleExtent([1, Infinity]) // TODO: set maximum zoom
            .translateExtent([[0, 0], [width, subHeight]])
            .extent([[0, 0], [width, subHeight]])
            .on("zoom", zoomed);

        function brushed() {
            var s = d3.event.selection;
            if (d3.event.sourceEvent && d3.event.sourceEvent.type === "zoom") return; // ignore brush-by-zoom
            let t = d3.zoomIdentity
                .scale(width / (s[1] - s[0]))
                .translate(-s[0], 0);
            zoom.transform(container, t);
            draw(t);
            updateAnnotations(t);
        }

        function zoomed() {
            if (d3.event.sourceEvent && d3.event.sourceEvent.type === "brush") return; // ignore zoom-by-brush
            var t = d3.event.transform;
            draw(t);
            updateAnnotations(t);
            gbrush.call(brush.move, xScale.range().map(t.invertX, t))
        }

        if (!scrubber) {
            container.call(zoom);
            brush.on("brush.line end", brushed);

            // Set default zoom level
            zoom.scaleBy(container, 2.0);
        }
    }

    addSpectrogram(
        wrapper: HTMLDivElement,
        brush: d3.BrushBehavior<{}>,
        gbrush,
    ) {
        const windowSize = 128;
        const hopSize = 128;
        const xdata = this._data.xdata;
        const ydatas = this._data.ydatas;
        const nys = ydatas.length;

        // TODO consider using typed arrays here.
        let mags: Array<Array<Array<Number>>> = []
        for (let y = 0; y < nys; y++) {
            let mag = []
            function onFreq(re, im) {
                let m = []
                for (let i = 0; i < re.length / 2 + 1; i++) {
                    let power = 10 * Math.log10(re[i] * re[i] + im[i] * im[i])
                    // TODO deal with infinite power better.
                    if (Math.abs(power) !== Infinity) {
                        m.push(power);
                    }
                }
                mag.push(m);
            }
            let stft = shortTimeFT.stft(windowSize, onFreq, { hop_size: hopSize });
            let ydata = ydatas[y];
            for (let i = 0; i < ydata.length; i += windowSize) {
                let temparray = new Float32Array(windowSize);
                for (let j = 0; i + j < ydata.length; j++) {
                    temparray[j] = ydata[i + j];
                }
                stft(temparray);
            }
            mags.push(mag);
        }

        // Add canvases to DOM
        const bbox = wrapper.getBoundingClientRect();
        const outerWidth = bbox.width;
        let outerHeight = bbox.height / nys;
        const margin = {
            top: outerHeight / 20 / nys,
            right: 15,
            bottom: outerHeight / 10 / nys,
            left: 70
        };
        const width = outerWidth - margin.left - margin.right;
        const height = outerHeight - margin.top - margin.bottom;
        let contexts = [];
        const container = d3.select(wrapper);
        for (let i = 0; i < nys; i++) {
            const div = container.append('div')
                .attr('width', outerWidth)
                .attr('height', outerHeight)
                .style('position', 'relative')
                .style('top', i * outerHeight + 'px');
            const canvasChart = div.append('canvas')
                .attr('width', width)
                .attr('height', height)
                .style('margin-left', margin.left + 'px')
                .style('margin-top', margin.top + 'px')
                .attr('class', 'canvas-plot');
            const canvas = <HTMLCanvasElement>canvasChart.node()
            const context = canvas.getContext('2d');
            contexts.push(context);
        }

        // Init Scales
        const xScale = d3.scaleLinear()
            .domain([0, mags[0].length])
            .range([0, width])
        const yScale = d3.scaleLinear()
            .domain([0, mags[0][0].length])
            .range([height, 0])

        const min = Number(d3.min(mags, d => d3.min(d, d => d3.min(d))));
        const max = Number(d3.max(mags, d => d3.max(d, d => d3.max(d))));
        const zScale = d3.scaleLinear()
            .domain([min, max])
            .range(["red", "blue"] as any)
            .interpolate(d3.interpolateHslLong as any);

        const dotWidth = width / mags[0].length;
        const dotHeight = height / mags[0][0].length;

        function draw(transform: d3.ZoomTransform) {
            const xScaleTranformed = transform.rescaleX(xScale);
            const start = Math.floor(transform.x * -1 / transform.k / width * mags[0].length);
            const end = start + Math.ceil(mags[0].length / transform.k);
            window.requestAnimationFrame(function () {
                for (let y = 0; y < nys; y++) {
                    let context = contexts[y];
                    context.clearRect(0, 0, width, height);
                    let mag = mags[y];
                    for (let i = start; i < end; i++) {
                        for (let j = 0; j < mag[0].length; j++) {
                            let z = mag[i][j];
                            context.fillStyle = zScale(z);
                            context.fillRect(xScaleTranformed(i), yScale(j),
                                transform.k * dotWidth, dotHeight);
                        }
                    }
                }
            });
        }
        draw(d3.zoomIdentity
            .scale(2)
            .translate(-width / 4, 0));

        function brushed() {
            var s = d3.event.selection;
            let t = d3.zoomIdentity
                .scale(width / (s[1] - s[0]))
                .translate(-s[0], 0);
            draw(t);
        }
        brush.on("brush.spectrogram end", brushed);
    }
}

module.exports = LineChart;