import * as d3 from "d3";
import { Data, Annotation } from "./types";
const LineChart = require('./line-chart')

let chart = new LineChart(document.getElementById("main"));

var fileSelect = document.getElementById("fileSelect"),
  fileElem = document.getElementById("fileElem");

fileSelect.addEventListener("click", function (e) {
  if (fileElem) {
    fileElem.click();
  }
}, false);

var epochSelect: HTMLSpanElement = document.getElementById("epoch");
var recordingSelect: HTMLSpanElement = document.getElementById("recording");
var previousSelect = <HTMLButtonElement>document.getElementById("previous");
var nextSelect = <HTMLButtonElement>document.getElementById("next");

var datas: Data[];
var datasIndex = 0;

function updateRecording() {
  recordingSelect.innerText = datasIndex + 1 + ' / ' + datas.length;
}

function handlePrevNext(next: boolean) {
  let update = false;
  if (next) {
    if (datasIndex < datas.length - 1) {
      datasIndex++;
      update = true;
    }
  } else {
    if (datasIndex > 0) {
      datasIndex--;
      update = true;
    }
  }
  if (update) {
    updateRecording();
    chart.data = datas[datasIndex];
  }
}

previousSelect.onclick = () => handlePrevNext(false);
nextSelect.onclick = () => handlePrevNext(true);

function handleFiles(evt) {
  let files: FileList = evt.path[0].files;
  if (files.length !== 2) {
    window.alert("Upload two files: samples and annotations");
    return;
  }
  // Expected file name format: RNS1090_PE20170530_edited_eof_edited_annotations.txt
  let dataFile, annotationsFile: File;
  for (let file of files) {
    if (file.name.endsWith('eof_edited_annotations.txt')) {
      annotationsFile = file;
    } else if (file.name.endsWith('eof_edited_data.txt')) {
      dataFile = file;
    } else {
      window.alert("Unexpected file name format: " + file.name);
    }
  }

  let epochName = files[0].name.split('_').slice(0, 2).join(" ");
  epochSelect.innerText = epochName;

  function readAnnotations(file: File): Promise<Annotation[]> {
    return new Promise(function (resolve, reject) {
      let r = new FileReader()
      r.onloadend = (evt: any) => {
        let data = d3.csvParse(evt.target.result,
          d => {
            return {
              onset: parseFloat(d.Onset),
              name: d.Annotation,
            }
          }
        );
        resolve(data as Annotation[]);
      };
      r.readAsText(file);
    });
  }

  function readData(file: File, annotations: Annotation[]): Promise<Data[]> {
    return new Promise(function (resolve, reject) {
      let r = new FileReader()
      r.onloadend = (evt: any) => {
        let rows = d3.csvParse(evt.target.result);
        let datas: Data[] = [];
        let start = 0;
        let bisector = d3.bisector((row: d3.DSVRowString) => parseFloat(row.Time)).right;
        let nonEofAnnotations = [];
        annotations.map(annotation => {
          if (annotation.name !== 'eof') {
            nonEofAnnotations.push(annotation);
            return;
          }
          let end = bisector(rows, annotation.onset);
          let length = end - start;
          let data: Data = {
            xdata: new Float32Array(length),
            ydatas: [new Float32Array(length),
            new Float32Array(length),
            new Float32Array(length),
            new Float32Array(length)],
            annotations: nonEofAnnotations,
          };
          nonEofAnnotations = [];
          for (let i = start; i < end; i++) {
            const row = rows[i];
            let j = i - start;
            data.xdata[j] = parseFloat(row.Time);
            data.ydatas[0][j] = parseFloat(row[1]);
            data.ydatas[1][j] = parseFloat(row[2]);
            data.ydatas[2][j] = parseFloat(row[3]);
            data.ydatas[3][j] = parseFloat(row[4]);
          }

          start = end;
          datas.push(data);
        });
        if (start !== rows.length) {
          window.alert("missing eof");
        }
        resolve(datas);
      };
      r.readAsText(file);
    });
  }

  readAnnotations(annotationsFile)
    .then((annotations) => readData(dataFile, annotations)
      .then((ds) => {
        datas = ds;
        console.log(datas);
        chart.data = datas[datasIndex];
        updateRecording();
      }));
}

fileElem.onchange = handleFiles;